

export class User {
  public fname: string;
  public lname: string;
  public dob: string;
  public email: string;
  public gender: string;
  public  uid: string;
  constructor(  fname: string, lname: string, dob: string, email: string, gender: string, uid: string ) {
    this.fname = fname;
    this.lname = lname;
    this.dob = dob;
    this.email = email;
    this.gender = gender;
    this.uid = uid;
  }

}



