import {Component, HostListener, OnInit} from '@angular/core';
import { NgForm } from '@angular/forms';

import { AuthService } from '../auth.service';
import {Subscription} from 'rxjs/Subscription';
import {MaterializeDirective, MaterializeAction} from 'angular2-materialize';
import {fadeInAnimation} from '../../animations/fadeInAnimation';
import {DataStorageService} from "../../shared/data-storage.service";
declare var Materialize: any;
@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css'] ,
  animations: [fadeInAnimation],
})

export class SigninComponent implements OnInit {

  private subscription: Subscription;
  private dataSubscription: Subscription;
  errorOutput: string;
  flag: boolean;
  constructor(private authService: AuthService, private dataService: DataStorageService) { }

  ngOnInit() {
  }

  onSignin(form: NgForm) {

    const lname = form.value.lname;
    const dob = form.value.dob;
    const gender = form.value.gender;
    const fname = form.value.fname;
    const email = form.value.email;
    const password = form.value.password;
    this.authService.signinUser(email, password);
    this.dataSubscription = this.authService.signinSuccess.subscribe(
    (success: string) => {
      console.log('hh');
      this.dataService.getRecipesbyUID();
    }
    );
    this.subscription = this.authService.errorGenerated.subscribe(
      (error: string) => {
        this.errorOutput = error;
        Materialize.toast(this.errorOutput, 4000, 'bold');
      }
    );
  }
  materializeToastcallonce(text: string, duration: number = 300, style: string) {

  }



}



