import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {DataStorageService} from '../shared/data-storage.service';
import {AccountService} from './account.service';
import {Subscription} from 'rxjs/Subscription';
import {User} from '../shared/user.model';
import '../../dropscripts.js' ;
import {fadeInAnimation} from '../animations/fadeInAnimation';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css'] ,
  animations: [fadeInAnimation]
})

export class AccountComponent implements OnInit {

  formGrp: FormGroup;
  subscription: Subscription;
  users: User[];
  obj: Object[];
  flag = false;
  editMode = false;
  keys: string[];
  currentUser: User;
  constructor( private accService: AccountService) { }


ngOnInit () {
this.subscription = this.accService.usersChanged.subscribe(
  (users: User[]) => {
    this.obj = users;
    this.users = users;
    this.keys = Object.keys(this.users);
    for (const key of this.keys ) {
      if (this.users[key].uid === this.accService.getUID()) {
        this.currentUser = this.users[key];
      }
    }
  });
}

  initform() {
    let fname = '';
    let lname = '';
    let dob = '';
    let gender = '';
    let email = '';

    if (this.flag) {
      fname = this.currentUser.fname;
      lname = this.currentUser.lname;
      dob = this.currentUser.dob;
      gender = this.currentUser.gender;
      email = this.currentUser.email;
    }
    this.formGrp = new FormGroup({
        'fname': new FormControl(fname),
        'lname': new FormControl(lname),
        'dob': new FormControl(dob),
        'gender': new FormControl(gender),
        'email': new FormControl(email)
    });
  }

fetchUsers() {

  this.keys = Object.keys(this.users);
    this.flag = true;
    for (const key of this.keys ) {
      if (this.users[key].uid === this.accService.getUID()) {
        this.currentUser = this.users[key];
        this.initform();
      }
    }
    return this.currentUser;
  }

  onEdit() {

  }
  onCancel() {

  }
}



