

import {Injectable} from '@angular/core';
import {DataStorageService} from '../shared/data-storage.service';
import {Recipe} from '../recipes/recipe.model';
import {Subject} from 'rxjs/Subject';

declare var Materialize: any;
export interface IRecipe {
  description?: string ;
  imagePath?: string ;
  ingredients?: any[] ;
  name?: string ;
}

@Injectable()
export class MyRecipeService {
  IRecipes: IRecipe[] = [];
  recipes: Recipe[];
  myrecipesChanged = new Subject<IRecipe[]>();
  constructor() {}

 getMyRecipes() {
  return this.IRecipes;
 }

 setMyRecpies (recipes: IRecipe[]) {
    this.IRecipes = recipes;
    this.myrecipesChanged.next(this.IRecipes.slice());
 }
  restoremyRecipe(recipe: IRecipe[]) {
    for (const i of recipe) {
      this.IRecipes.push(i);
    }

    this.myrecipesChanged.next(this.IRecipes.slice());
  }
 deletemyRecipe(key: number) {
   this.IRecipes.splice( key, 1);
   this.myrecipesChanged.next(this.IRecipes.slice());
 }
 addMyRecipes (recipe: IRecipe) {
   let flag = false;
   if (this.IRecipes.length !== 0) {

   for (const i of this.IRecipes) {
     if (i.name === recipe.name  ) {
       flag = false;
       break;
     } else {
       flag = true;
       continue;
     }
   }
 } else {
     flag = true;
   }
// has to click on my recipes tag to load data
      if (flag) {
     Materialize.toast('Recipe added', 4000);
        this.IRecipes.push(recipe);
        this.myrecipesChanged.next(this.IRecipes.slice());
   } else {

        Materialize.toast('Duplicate Recipe', 4000);
      }








 }

 }



