import { Component, OnInit } from '@angular/core';
import { EmailService, IMessage} from './emailservice';
import { NgModel } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import {materialize} from 'rxjs/operator/materialize';
declare var Materialize: any;
@Component({
  selector: 'app-email-component',
  templateUrl: './email-component.html',
  styleUrls: ['./email-component.css']
})
export class EmailComponent implements OnInit {

  title = ' Email ';
  message: IMessage = {};
  constructor(private appService: EmailService) { }

  ngOnInit() {
  }


  sendEmail(message: IMessage) {
    this.appService.sendEmail(message).subscribe(res => {
      Materialize.toast('Email Sent', 4000, 'bold');
     // console.log('AppComponent Success', res);
    }, error => {
      Materialize.toast('Email Not Sent', 4000, 'bold');
     // console.log('AppComponent Error', error);
    });
  }
}



