import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import 'rxjs/Rx';
import { RecipeService } from '../recipes/recipe.service';
import { Recipe } from '../recipes/recipe.model';
import { AuthService } from '../auth/auth.service';
import {AccountService} from '../account/account.service';
import {User} from './user.model';
import {MyRecipeService} from '../myrecipe/myrecipe.service';


export interface IRecipe {
  description?: string ;
  imagePath?: string ;
  ingredients?: any[] ;
  name?: string ;
}

@Injectable()


export class DataStorageService {
  users: User[];
  body: any[];
  IRecpies: IRecipe[] = [];
  constructor(private http: Http,
              private recipeService: RecipeService,
              private authService: AuthService,
              private userService: AccountService,
              private myrecipeService: MyRecipeService
              ) {
  }

  storeRecipes() {
    const token = this.authService.getToken();
    const uid = this.authService.getUID();
    const recipe = this.myrecipeService.getMyRecipes();

    return this.http.put('https://mywebsite-1cdd3.firebaseio.com/recipes.json?auth='
      + token,
      JSON.stringify(this.recipeService.getRecipes()));
  }
storeRecipesbyUID() {
    const token = this.authService.getToken();
    const uid = this.authService.getUID();
    const recipe = this.myrecipeService.getMyRecipes();
     /* this.body = [{uid}, {recipe}];*/
    return this.http.put('https://mywebsite-1cdd3.firebaseio.com/recipes+'
      + uid +
      '.json?auth='
      + token,
      JSON.stringify(this.myrecipeService.getMyRecipes()));
  }

  getRecipesbyUID() {
    const token = this.authService.getToken();
    const uid = this.authService.getUID();
    this.http.get('https://mywebsite-1cdd3.firebaseio.com/recipes+' + uid + '.json?auth=' + token)
      .map(
        (response: Response) => {
          const recipes: Recipe[] = response.json();
          this.IRecpies = response.json();
          if (this.IRecpies !== null) {
            for (const recipe of recipes) {
              if (!recipe['ingredients']) {
                recipe['ingredients'] = [];
              }
            }
          } else {
            this.IRecpies = [] ;

          }
          return this.IRecpies;
        }

      )
      .subscribe(
        (recipes: IRecipe[]) => {
          this.myrecipeService.setMyRecpies(recipes);
        }
      );

  }

  /*storeUserData(userdata: User) {
    const token = this.authService.getToken();
    return this.http.put('https://mywebsite-1cdd3.firebaseio.com/users.json?auth=' + token, this.authService.userdata);
  }*/
  getRecipes() {
    const token = this.authService.getToken();

    this.http.get('https://mywebsite-1cdd3.firebaseio.com/recipes.json?auth=' + token)
      .map(
        (response: Response) => {
          const recipes: Recipe[] = response.json();
          for (const recipe of recipes) {
            if (!recipe['ingredients']) {
              recipe['ingredients'] = [];
            }
          }
          return recipes;
        }
      )
      .subscribe(
        (recipes: Recipe[]) => {
          this.recipeService.setRecipes(recipes);
        }
      );
  }

  getAccData() {
    const token = this.authService.getToken();

     this.http.get('https://mywebsite-1cdd3.firebaseio.com/userdetails.json?auth=' + token)
      .map(
        (response: Response) => {

          const data = response.json();

          return data || {};
        }
      ).subscribe(
        (users: User[]) => {
          this.users = users;
          this.userService.setAccData(this.users);
        }
      );

  }
}



