import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

import { AuthService } from '../auth.service';
import {Subscription} from 'rxjs/Subscription';
import {MaterializeModule} from 'angular2-materialize';
import { fadeInAnimation } from '../../animations/fadeInAnimation';
declare var Materialize: any;
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css'] ,
  animations: [fadeInAnimation]
})
export class SignupComponent implements OnInit {
errorOutput: string;
selectedEntry: any;
  private subscription: Subscription;
  constructor(private authService: AuthService) { }

  ngOnInit() {
  }

  onSignup(form: NgForm) {

    const email = form.value.email;
    const password = form.value.password;
    const fname = form.value.fname;
    const lname = form.value.lname;
    const dob = form.value.dob;

   this.authService.signupUser(fname, lname, dob, this.selectedEntry, email, password);
    this.subscription = this.authService.errorGenerated.subscribe(
      (error: string) => {
        console.log(error);
        this.errorOutput = error;
        Materialize.toast(this.errorOutput, 4000, 'bold');
      });
  }
  onSelectionChange(entry) {
    this.selectedEntry =  entry;
  }
}



