import { Component, OnInit } from '@angular/core';
import {DataStorageService} from '../../shared/data-storage.service';
import {Recipe} from '../../recipes/recipe.model';
import {Subscription} from 'rxjs/Subscription';
import {MyRecipeService} from '../myrecipe.service';
import {IRecipe} from '../../shared/data-storage.service';
import {ActivatedRoute, Router} from '@angular/router';
import { Response } from '@angular/http';
import {fadeInAnimation} from '../../animations/fadeInAnimation';
declare var Materialize: any;

@Component({
  selector: 'app-myrecipelist',
  templateUrl: './myrecipelist.component.html',
  styleUrls: ['./myrecipelist.component.css'] ,
  animations: [fadeInAnimation]
})
export class MyrecipelistComponent implements OnInit {

 IRecipes: IRecipe[] = [];
 deletedRecipes: IRecipe[] = [];
  subscription: Subscription;
  deleteSubscription: Subscription;
  recipes: Recipe[];
  flag = false;
  keys: string[];
  constructor(public myrecipeService: MyRecipeService,
              private route: ActivatedRoute,
              private router: Router, private dataService: DataStorageService) { }

  saveData() {

   this.dataService.storeRecipesbyUID().subscribe(
     (response: Response) => {
       this.deletedRecipes.splice(0, this.deletedRecipes.length);
       this.flag = false;

     }
   );
  }
  ngOnInit() {

    this.subscription = this.myrecipeService.myrecipesChanged
      .subscribe(
        (recipes: IRecipe[]) => {

          this.IRecipes = recipes;

          this.keys = Object.keys(this.IRecipes);
        }
      );
    this.IRecipes = this.myrecipeService.getMyRecipes();

  }
  onRestore() {
/*this.IRecipes.push(this.IRecipe);*/
this.myrecipeService.restoremyRecipe(this.deletedRecipes.slice());
    this.deletedRecipes.splice(0, this.deletedRecipes.length);
this.flag = false;
  }
  onDelete(key: number) {

    this.deletedRecipes.push(this.IRecipes[key]);
   this.myrecipeService.deletemyRecipe(key);
    this.flag = true;
    /*  this.deleteSubscription = this.myrecipeService.myrecipesChanged.subscribe(
       (recipe: IRecipe[]) => {
         console.log(recipe);
         Materialize.toast('I am a toast', 4000, ' ' );
       }
     );*/
  }

}



