import {Injectable, OnInit} from '@angular/core';
import { Subject } from 'rxjs/Subject';

import { Recipe } from './recipe.model';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from '../shopping-list/shopping-list.service';
import {DataStorageService} from '../shared/data-storage.service';

@Injectable()
export class RecipeService  {



  recipesChanged = new Subject<Recipe[]>();

  private recipes: Recipe[] = [
    new Recipe(
      'Tasty Schnitzel',
      'A super-tasty Schnitzel - just awesome!',
      'https://upload.wikimedia.org/wikipedia/commons/7/72/Schnitzel.JPG',
      [
        new Ingredient('Meat', 1),
        new Ingredient('French Fries', 20)
      ]),
    new Recipe('Turkey sloppy Joes',
      'What else could you ask?',
      'https://media4.s-nbcnews.com/j/newscms/2015_33/730026/slow-cooker-hamburger-soup-tease-today-150813_b4a5172f749985f83c57548a6bc30bfb.today-inline-large.jpg',
      [
        new Ingredient('Buns', 2),
        new Ingredient('Meat', 1)
      ]),
    new Recipe(' Cheesy tortellini',
      'Packed with sausage, beef and cheese',
      'https://media2.s-nbcnews.com/j/newscms/2015_33/730011/slow-cooker-tortellini-tease-today-150813_9de8dd804a80106294b827380a2bdfaf.today-inline-large.jpg',
      [
        new Ingredient('Buns', 2),
        new Ingredient('Meat', 1)
      ]),
    new Recipe('Hearty Beef Stew',
      'The recipe is from Lion House cookbooks',
      'http://www.seriouseats.com/images/2015/09/20150914-pressure-cooker-recipes-roundup-13.jpg',
      [
        new Ingredient('Buns', 4),
        new Ingredient('Meat', 2)
      ]),
    new Recipe('Filipino Chicken Adobo',
      'A burst of sweet, savory, and sour flavors',
      'https://www.pressurecookrecipes.com/wp-content/uploads/2016/03/filipino-pressure-cooker-chicken-adobo-featured.jpg',
      [
        new Ingredient('Fries', 2),
        new Ingredient('Meat', 3)
      ]),
    new Recipe('Chicken With Chickpeas and Chorizo',
      'Spanish chorizo and smoked paprika',
      'http://www.seriouseats.com/images/2015/09/20150914-pressure-cooker-recipes-roundup-09.jpg',
      [
        new Ingredient('Fries', 2),
        new Ingredient('Meat', 3)
      ])
  ];


  constructor(private slService: ShoppingListService) {}


  setRecipes(recipes: Recipe[]) {
    this.recipes = recipes;
    this.recipesChanged.next(this.recipes.slice());
  }

  getRecipes() {
    return this.recipes.slice();
  }

  getRecipe(index: number) {
    return this.recipes[index];
  }

  addIngredientsToShoppingList(ingredients: Ingredient[]) {
    this.slService.addIngredients(ingredients);
  }

  addRecipe(recipe: Recipe) {
    this.recipes.push(recipe);
    this.recipesChanged.next(this.recipes.slice());
  }

  updateRecipe(index: number, newRecipe: Recipe) {
    this.recipes[index] = newRecipe;
    this.recipesChanged.next(this.recipes.slice());
  }

  deleteRecipe(index: number) {
    this.recipes.splice(index, 1);
    this.recipesChanged.next(this.recipes.slice());
  }
}



