import { Router } from '@angular/router';
import * as firebase from 'firebase';
import { Injectable } from '@angular/core';
import { Headers, Http, Response } from '@angular/http';
import {Subject} from 'rxjs/Subject';
import {User} from '../shared/user.model';
import {RecipeService} from '../recipes/recipe.service';
import {AccountService} from '../account/account.service';
import {DataStorageService} from '../shared/data-storage.service';



declare var Materialize: any;
@Injectable()
export class AuthService {
  errorGenerated = new Subject<string>();
  signinSuccess = new Subject<string>();
  token: string;
  error: string;
  userdata: User;
  constructor(private router: Router,
              private http: Http) {}

   saveUserData(fname: string, lname: string, dob: string, email: string, gender: string, token: string, uid: string) {
    this.userdata = new User(fname, lname, dob, email, gender, uid);
          this.storeUserData(this.userdata, token).subscribe(
                        (response: Response) => {
                        });
   }

  signupUser(fname: string, lname: string, dob: string, gender: string, email: string, password: string) {
    firebase.auth().createUserWithEmailAndPassword(email, password).then(
      response => {
       this.signinUser(email, password);
        firebase.auth().currentUser.getToken()
          .then(
            (token: string) => {

              this.saveUserData(fname, lname, dob, email, gender, token, firebase.auth().currentUser.uid);
            });
        Materialize.toast('Sign up successful', 2000);
      })
      .catch(
        error => {
          this.error = error.message;
          console.log(this.error);
          this.errorGenerated.next(this.error);
          this.errorGenerated = new Subject<string>();
        });
  }
  storeUserData(user: User, token: string) {
    const token2 = token;
    const headers = new Headers({'Content-Type': 'application/json'});
    return this.http.post('https://mywebsite-1cdd3.firebaseio.com/userdetails.json?auth='
      + token2,
      JSON.parse(JSON.stringify(user)),
      { headers: headers});
  }
  signinUser(email: string, password: string) {

    firebase.auth().signInWithEmailAndPassword(email, password)
      .then(
        response => {


          firebase.auth().currentUser.getToken()
            .then(
              (token: string) => {

                this.token = token;
                Materialize.toast('Signed in', 4000, 'bold');
                this.router.navigate(['/']);
                this.signinSuccess.next('success');
              });
        })
      .catch(
        error => {
          this.errorGenerated.next(error.message);
          this.errorGenerated = new Subject<string>();
        });
  }

  logout() {
    firebase.auth().signOut();
    this.router.navigate(['/signin']);
    this.token = null;
  }

  getToken() {

    firebase.auth().currentUser.getToken()
      .then(
        (token: string) => this.token = token
      );
    return this.token;
  }

  isAuthenticated() {
    return this.token != null;
  }
  getUID () {
    return firebase.auth().currentUser.uid;
  }
}



